@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap);

* {
  font-family: "Space Grotesk", sans-serif;
}
body {
  margin: 0;
}
.App {
  background-color: #01030d;
}

.front {
  position: relative;
  text-align: center;
  color: white;
  overflow: hidden;
}
.front > img {
  width: 120%;
  height: 150%;
  margin-left: -10%;
}
.front-flex {
  position: absolute;
  top: 50px;
  left: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  /* transform: translate(0%, 0); */
}
.front-flex > img {
  height: 40px;
  width: auto;
  padding-right: 20px;
}
.front-flex > h2 {
  margin-left: 0;
  font-size: 30px;
  font-weight: 700;
  line-height: 32px;
  margin-right: auto;
}
.icons {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}
.icons > a {
  text-decoration: none;
  color: #000;
  font-size: 20px;
  padding: 15px 15px 5px 15px;
  border: 1px solid #fff;
  transition: background-color 0.5s ease-in-out;
}
.icons > a:hover {
  background-color: #fff;
}
.front > a {
  position: absolute;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  padding: 20px 30px;
  background-color: #4753d6;
  transition: all 0.3s ease-in-out 0s;
  box-sizing: border-box;
}
.front > a:hover {
  background-color: #6c77ea;
}

.section1 {
  padding-top: 5%;
  margin-top: -15%;
  -webkit-clip-path: polygon(0% 0%, 50% 15%, 100% 0%, 100% 100%, 0% 100%);
          clip-path: polygon(0% 0%, 50% 15%, 100% 0%, 100% 100%, 0% 100%);
  background-color: #01030d;
  color: #fff;
  padding-bottom: 100px;
  min-height: 100vh;
}
.section1-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: auto;
  padding-top: 250px;
  grid-gap: 80px;
}
.grid-section1 {
  display: flex;
  flex-direction: column;
  padding: 20px 0px;
}
.grid-section1 > h1 {
  font-size: 46px;
  font-weight: 700;
  color: #dc3b73;
  margin: 0;
}
.grid-section1 > h2 {
  font-size: 46px;
  font-weight: 700;
  margin: 0;
}
.grid-section1 > p {
  font-size: 18px;
  line-height: 32px;
  color: #848198;
}
.section1-grid > img {
  height: 400px;
  margin: auto;
}

.section2 {
  color: #fff;
  min-height: 70vh;
  width: 80%;
  margin: auto;
  justify-content: center;
  align-items: center;
}
.center > img {
  margin: auto;
  width: 90%;
}

.section3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60%;
  margin: auto;
  color: #fff;
}
.section3 > h1 {
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 0;
}
.section3 > p {
  font-size: 18px;
  max-width: 600px;
  line-height: 32px;
  text-align: center;
  color: #78758b;
  margin-bottom: 150px;
}
.timeline {
  padding: 50px;
  padding-left: 150px;
  background-color: #04091a;
  margin-bottom: 50px;
}
.timeline > h1 {
  font-size: 30px;
  font-weight: 700;
  line-height: 33px;
}
.timeline > h1 > span {
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  padding-right: 20px;
  color: #ed588c;
}
.timeline > p {
  font-size: 18px;
  line-height: 32px;
  color: #848198;
}

.section4 {
  display: grid;
  grid-template-columns: auto 1fr;
  color: #fff;
  width: 70%;
  margin: auto;
  grid-gap: 80px;
  padding: 150px 0px;
  /* min-height: 60vh; */
}
.section4-head {
  text-align: right;
  display: flex;
  flex-direction: column;
  /* margin: auto; */
  justify-content: center;
}
.section4-head > h2 {
  margin: 0;
  color: #848198;
  font-size: 32px;
  font-weight: 700;
}
.section4-head > h1 {
  margin: 0;
  color: #ee5b8f;
  font-size: 46px;
  font-weight: 700;
}
.section4-content {
  color: #848198;
  font-size: 25px;
  font-weight: 200;
  margin: auto;
}
.section4-content > p {
  font-size: 18px;
  line-height: 32px;
}

.section5 {
  display: grid;
  grid-template-columns: 1fr 3fr;
  color: #fff;
  width: 70%;
  margin: auto;
  grid-gap: 120px;
  padding: 150px 0px;
}

.inline-flex {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inline-flex > h1 {
  font-size: 30px;
  font-weight: 700;
  margin-right: auto;
  margin-left: 0;
}
.inline-flex > h2 {
  font-size: 22px;
  line-height: 40px;
  font-weight: 700;
  margin-right: auto;
  margin-left: 0;
}
.section5-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: 0;
  grid-gap: 20px;
  gap: 20px;
}
.section5-icons > a {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  padding: 10px 10px 5px 10px;
  background-color: #4753d6;
}
.border-right {
  padding: 0px 50px;
  border-right: 1px solid #202946;
}
.section5-grid1 > img {
  width: 20vw;
}
.seaction5-center > img {
  width: 90%;
}

.section6 {
  width: 100%;
  min-height: 500px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}
.section6-content {
  display: flex;
  justify-content: center;
  /* align-items: left; */
  width: 50%;
  flex-direction: column;
  margin-left: 10%;
}
.section6-content > h1 {
  font-size: 50px;
  margin: 0;
}
.section6-content > p {
  font-size: 18px;
  line-height: 32px;
  color: #78758b;
  max-width: 450px;
  margin-bottom: 50px;
}
.section6-content > a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  line-height: 32px;
  padding: 20px 30px;
  background-color: #4753d6;
  width: -webkit-max-content;
  width: max-content;
}

.footer {
  width: 100%;
  min-height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-grid {
  width: 90%;
  display: grid;
  grid-template-columns: 3fr 1fr;
  color: #fff;
}
.footergrid1 {
  display: flex;
  justify-content: center;
  align-self: start;
  flex-direction: column;
}
.flex {
  padding-top: 50px;
  display: flex;
  justify-content: center;
  align-self: start;
  margin: 0;
  grid-gap: 20px;
  gap: 20px;
}
.flex > img {
  height: 70px;
  width: auto;
}
.footergrid1 > h3 {
  font-size: 18px;
  font-weight: 300;
  margin: 0;
}
.footergrid1 > p {
  padding-top: 50px;
  font-size: 15px;
  font-weight: 300;
  color: #848198;
}
.footergrid2 {
  padding-top: 50px;
  display: flex;
  justify-content: center;
  align-self: start;
  flex-direction: column;

  color: #2a3284;
}
.footergrid2 > h1 {
  margin: 0;
  font-size: 16px;
  line-height: 32px;
  font-weight: 500;
}
.blue {
  color: #fff;
}
.footer-icons {
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 0;
  margin-right: auto;
  grid-gap: 20px;
  gap: 20px;
}
.footer-icons > a {
  text-decoration: none;
  color: #ffffff;
  font-size: 20px;
  padding: 10px 10px 5px 10px;
  border: 1px solid #4753d6;
  transition: background-color 0.5s ease-in-out;
}
.footer-icons > a:hover {
  background-color: #4753d6;
}

